html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;

  box-sizing: inherit;
}

body {
  min-width: 320px;

  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;

  color: $black;
  background-color: $white;
}

a {
  text-decoration: none;
}

img {
  display: block;

  max-width: 100%;
  height: auto;
}
